import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//动画：第三方库引入
import animate from "animate.css";
//移动端300ms延迟解决安装：npm install  fastclick --save
import FastClick from 'fastclick'
//图片懒加载插件，安装命令：npm install vue-loading --save
import VueLazyLoad from 'vue-lazyload'
//解决移动端300ms延迟
FastClick.attach(document.body)
//图片懒加载
Vue.use(VueLazyLoad,{
  loading:require('./assets/img/shouye/lanjiajia.png')
})
Vue.config.productionTip = false
Vue.use(animate);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
