import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    arr: [],
    zhuantai: false,

  },
  mutations: {
    quanbu(state) {
      let flog = 0;
      for (let i = 0; i < state.arr.length; i++) {
        if (state.arr[i].isshow == false) {
          if(flog==0){
            flog = 1;
          }
        state.arr[i].isshow = true;
        }
      }
      if (flog == 0) {
        for (let i = 0; i < state.arr.length; i++) {
          state.arr[i].isshow = false;
        }
      }
    },
    //
    arrjia(state, a) {
      let flog = 0
      for (let i = 0; i < state.arr.length; i++) {
        if (state.arr[i].id === a.id) {
          state.arr[i].count += 1;
          flog = 1
        }
      }
      if (flog == 0) {
        a.count = 1;
        state.arr.push(a)//坑：如果连续打印出来5个值，一次看，前面打印的值会被修改为最后一次，除非打印一次看一次
      }
      /* let flog=null;
       for(let item of state.arr){
         if(item.id===a.id){
           flog=item
         }
       }
       if(flog){
         flog.count+=1
       }else{
         a.count=1;
         state.arr.push(a)
       }*/

    },


  },
  actions: {
  },
  modules: {
  }
})
