<template>
  <div @click="qiehuan" class="son">
    <div  v-show="!isa"><slot name="qqq">11</slot></div>
    <div v-show="isa"><slot name="www">11</slot></div>
   <p :class="{active:isa}"><slot name="sss">11</slot></p>
 </div>
</template>
<script>
export default {
  name: 'Shouyeer',
   props: {
   path:String
    },
  methods:{
    qiehuan(){
      if( this.$route.path.indexOf(this.path) !==-1) return;
       this.$router.replace(this.path);//通过父子通信获取path,必须加this
     // console.log(this.$route.path); 
    }
   
  },
  computed: {
    isa(){
      //活跃路由中是否包含当前活跃的路由状态
      return this.$route.path.indexOf(this.path) !==-1;
    }
  }
}
</script>
<style>
.son{
  
  flex:1;
  text-align: center;
  height: 52px;
}
.son div{
  display:inline-block;
}
.son div img{
width: 26px;
margin-top:2px;

}
.active{
  color:brown;
}
</style>
