import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const Fenlei= () =>import('../views/fenlei/Fenlei.vue');
const Shoucang= () =>import('../views/shoucang/Shoucang.vue');
const Shouyess= () =>import('../views/shouye/Shouyess.vue');
const Gouwuche= () =>import('../views/gouwuche/Gouwuche.vue');
const Detail= () =>import('../views/detail/Detail.vue');



const routes = [
  {
    path:'/detail/:id',//这个iid必须和传过去的iid一样
    component:Detail,
    },
  
  {
    path:'/fenlei',
    component:Fenlei,
    },
    {
      path:'/shoucang',
      component:Shoucang,
      },
      {
        path:'/gouwuche',
        component:Gouwuche,
        },
        {
          path:'/fenlei',
          component:Fenlei,
          },
          {
            path:'/shouyess',
            component:Shouyess,
          },
          {
            path:'',//默认路由路径为Home,即一打开是Home的网页
            redirect:'/shouyess' 
            },
          ]
            
         /* {
          path:'',//默认路由路径为Home,即一打开是Home的网页
          redirect:'/shouyess' 
          },*/





const router = new VueRouter({
 // mode: 'history',//打包后记得把这个模式去掉
  base: process.env.BASE_URL,
  routes
})

export default router
